/*
*   connect
*  storeconnect
*/
import React, { Component } from 'react';
import { connect } from 'react-redux'
import { is, fromJS} from 'immutable';
import *as action from '../Redux/Action/index';

export const template = mySeting => {
	 let seting = {
        id: '', //id
        url: '', //
        data: {}, //
        component: <div></div>, //
    };
    for(let key in mySeting){
    	seting[key] = mySeting[key];
    }
    class Index extends Component {
    	static defaultProps = { seting }
    	
    	constructor (props, context){
    		super(props, context)
    	}

    	render() {
            return <this.props.seting.component {...this.props} state={this.props.state.toJS()}/>;  // immutabeljs
        }
    }
    // Indexconnect
     
     return connect(state => { 
        
        let {loginOrNot, mailList, unRead, mailWords} = state;
        //console.log(loginOrNot,mailList,state)
        console.log(loginOrNot,mailList,unRead)
        return { 
        	state: state['fetchData'],
            loginOrNot,
            mailList,
            unRead,
            mailWords
        } 
    }, action)(Index); //redux
} 




